@use "~admin-stylesheets/colors";

.nab-url-original-version {

	&__url {
		margin-bottom: 0.8em;
		width: 100%;

		&-value {
			flex-grow: 1;
			margin-bottom: 1em;
			margin-right: 0.5em;

			.components-base-control__field {
				margin-bottom: 0;
			}
		}
	}

	&__actions {
		display: flex;
		visibility: hidden;
		flex-direction: column;
		justify-content: center;
		list-style: none;
	}
}

.nab-alternative-list__alternative--original:hover
	.nab-url-original-version__actions,
.nab-alternative-list__alternative--original:focus-within
	.nab-url-original-version__actions {
	visibility: visible;
}
